<template>
  <v-dialog v-model="dialog" :width="360" dark>
    <div class="register-info primaryDark">
      <div class="d-flex align-center">
        <v-icon class="mr-2 accent--text">mail_outline</v-icon>
        <h1 class="accent--text">Confirmar Cadastro!</h1>
      </div>
      <p class="white--text mt-4">
        Seu cadastro está quase completo.<br />
        Verifique sua caixa de e-mail e confirme o seu cadastro.
      </p>
      <div class="text-center mt-6" v-if="email">
        <btn-primary
          text="Reenviar e-mail"
          icon="send"
          :loading="loading"
          class="btn-send d-inline-block ma-auto"
          @click="sendMail"
        />
      </div>
      <div class="text-center mt-4">
        <btn-text
          text="Fechar"
          class="btn-confirm d-inline-block" 
          @clicked="dialog = false"
        />
      </div>
    </div>
  </v-dialog>
</template>

<script>
import BtnPrimary from '@/components/site/buttons/BtnPrimary'
import BtnText from '@/components/globais/buttons/text/BtnText'
import SnackbarMixin from '@/mixins/SnackbarMixin'
export default {
  components: { BtnPrimary, BtnText },
  mixins: [ SnackbarMixin ],
  data: () => ({
    dialog: false,
    email: null,
    loading: false
  }),
  methods: {
    onSendMailError (msg = 'Não foi possível enviar o e-mail!') {
      this.showSnackbarErro(msg)
    },
    sendMail () {
      this.loading = true
      this.$axios.post('/barber/resend-register-mail', {
        email: this.email
      }).then((response) => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.showSnackbar('E-mail enviado com sucesso!')
            this.dialog = false
          } else this.onSendMailError(response.data.corpo);
        } else this.onSendMailError()
      }).catch(() => this.onSendMailError())
      .finally(() => this.loading = false)
    },
    showDialog (email = null) {
      this.loading = false
      this.dialog = true
      this.email = email
    }
  }
};
</script>

<style lang="scss" scoped>
.register-info { padding: 30px !important;
  h1 { font-weight: 500; font-size: 1.5rem; }
  .btn-confirm { min-width: auto !important; width: 64px !important; }
}
.btn-send { width: 200px; }
</style>