<template>
  <v-dialog v-model="dialog" :width="320">
    <form-card title="Recuperar senha">
      <div>
        <label class="white--text label">E-mail:</label>
        <input-field
          label=""
          id="email"
          placeholder="Informe seu e-mail"
          :solo="true"
          :dark="true"
          :rules="emailRules"
          :value="email"
          type="email"
          class="input-box"
          @onKeyUp="setData"
        />
      </div>
      <btn-primary
        :disabled="!isFormValid"
        text="Logar"
        :loading="loading"
        class="mt-2 btn-recover-password d-block"
        @click="recoverPassword"
      />
    </form-card>
  </v-dialog>
</template>

<script>
import BtnPrimary from '@/components/site/buttons/BtnPrimary'
import FormCard from '@/components/site/FormCard'
import InputField from '@/components/globais/form/input-field/InputField'
import FormMixin from '@/mixins/FormMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import ValidationMixin from '@/mixins/ValidationMixin'
export default {
  components: { BtnPrimary, FormCard, InputField },
  mixins: [ FormMixin, SnackbarMixin, ValidationMixin ],
  data: () => ({
    email: null,
    dialog: false,
    loading: false
  }),
  computed: {
    isFormValid () {
      return this.isEmailValid(this.email)
    }
  },
  methods: {
    onRecoverPasswordError (msg = 'Não foi possível encaminhar o e-mail de recuperação de senha!') {
      this.showSnackbarErro(msg)
    },
    onRecoverPasswordSuccess () {
      this.$emit('onRecoverPassword')
    },
    recoverPassword () {
      this.loading = true
      this.$axios.post('/auth/barber/recovery-password', {
        email: this.email
      }).then(response => {
        if (Number(response.status) === 200) {
          if (Number(response.data.codigo) === 200) {
            this.onRecoverPasswordSuccess()
          } else this.onRecoverPasswordError(response.data.corpo)
        } else this.onRecoverPasswordError()
      }).catch (() => this.onRecoverPasswordError())
      .finally(() => this.dialog = false)
    },
    showDialog (email) {
      this.dialog = true
      this.email = email
    }
  }
}
</script>

<style scoped>
.btn-recover-password { width: 100%; }
</style>