<template>
  <div>
  <v-dialog v-model="dialog" :width="320">
    <div class="barber-login primaryDark pa-4 pa-sm-6" dark>
      <app-title text="Login" class="font-weight-regular" />
      <div class="mt-8">
        <div>
          <label class="white--text label">E-mail:</label>
          <input-field
            label=""
            id="email"
            placeholder="Informe seu e-mail"
            :solo="true"
            :dark="true"
            :rules="emailRules"
            class="input-box"
            type="email"
            @onKeyUp="setData"
          />
        </div>
        <div>
          <label class="white--text label">Senha:</label>
          <input-field
            label=""
            id="password"
            placeholder="Informe sua senha"
            :solo="true"
            :dark="true"
            type="password"
            class="input-box"
            :rules="passwordRules"
            @onKeyUp="setData"
          />
        </div>
        <btn-primary
          :disabled="!isFormValid"
          text="Logar"
          :loading="loading"
          class="mt-4 btn-login"
          @click="login"
        />
        <btn-text
          text="Esqueceu a senha?"
          class="white--text mt-4"
          @clicked="recoverPassword" 
        />
      </div>
    </div>
  </v-dialog>
  <barber-confirm-register-dialog ref="confirmDialog" />
  </div>
</template>

<script>
import AppTitle from '@/components/site/Title'
import BarberConfirmRegisterDialog from './BarberConfirmRegisterDialog'
import BtnPrimary from '@/components/site/buttons/BtnPrimary'
import BtnText from '@/components/globais/buttons/text/BtnText'
import BarberMixin from '@/mixins/BarberMixin';
import InputField from '@/components/globais/form/input-field/InputField'
import FormMixin from '@/mixins/FormMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import ValidationMixin from '@/mixins/ValidationMixin'
export default {
  components: { AppTitle, BarberConfirmRegisterDialog, BtnPrimary, BtnText, InputField },
  mixins: [ BarberMixin, FormMixin, SnackbarMixin, ValidationMixin ],
  data: () => ({
    dialog: false,
    email: null,
    loading: false,
    password: null
  }),
  computed: {
    isFormValid () {
      return this.isEmailValid(this.email) && this.isPasswordValid(this.password)
    }
  },
  methods: {
    login () {
      this.loading = true
      this.$axios.post('/auth/barber/login', {
        email: this.email,
        password: this.password
      })
      .then((response) => {
        if (response.data.codigo === 200) this.onLoginSuccess(response.data.corpo)
        else this.onLoginError(response.data.corpo)
      })
      .catch(() => this.onLoginError('Não foi possível realizar login na aplicação!'))
      .finally(() => this.loading = false)
    },
    onLoginError (text = 'Não foi possível realizar login!') {
      this.showSnackbarErro(text)
    },
    onLoginSuccess (token) {
      const routePath = localStorage.routePathAdmin
      this.storeBarber(token)
      const barber = this.getBarber()
      if (barber) {
        if (barber.enabled) {
          if (routePath && routePath !== this.$route.path) {
            localStorage.removeItem('routePathSite')
            this.goToPath(routePath)
          } else this.goTo('admin')
        } else this.showConfirmDialog()
      } else this.onLoginError('Não foi possível recuperar o token de acesso à aplicação!')
    },
    recoverPassword () {
      this.$emit('recoverPassword', this.email)
    },
    showDialog () {
      this.email = null
      this.password = null
      this.dialog = true
    },
    showConfirmDialog () {
      this.$refs.confirmDialog.showDialog(this.email)
    }
  }
}
</script>

<style>
.barber-login .btn-login { width: 100%; display: block; }
</style>