<template>
  <div class="barber-register accent">
    <div class="container form-container">
      <v-row>
        <v-col>
          <h1 class="title">
            Experimente grátis por 3 meses
          </h1>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" sm="6" md="3" class="py-0">
          <label>Estabelecimento:</label>
          <input-field
            id="barbershopName"
            label="" 
            :rules="nameRules"
            :solo="true"
            :value="barbershopName"
            @onKeyUp="setData"
          />
        </v-col>
        <v-col cols="12" sm="6" md="3" class="py-0">
          <label>Seu nome:</label>
          <input-field 
            id="barberName"
            label=""
            :rules="nameRules"
            :solo="true"
            :value="barberName"
            @onKeyUp="setData"
          />
        </v-col>
        <v-col cols="12" sm="6" md="3" class="py-0">
          <label>E-mail:</label>
          <input-field
            id="barberMail"
            label=""
            :rules="emailRules" 
            :solo="true"
            type="email"
            :value="barberMail"
            @onKeyUp="setData"
          />
        </v-col>
        <v-col cols="12" sm="6" md="3" class="py-0">
          <label>Senha:</label>
          <input-field
            id="barberPassword"
            label=""
            :rules="passwordRules"
            :solo="true"
            type="password"
            :value="barberPassword"
            @onKeyUp="setData"
          />
        </v-col>
      </v-row>
      <v-row>
        <v-spacer></v-spacer>
        <v-col class="pt-4 pt-md-0 text-center" cols="12" md="auto">
          <v-btn
            class="btn-register primary text-none"
            @click="register"
          >
            <template v-if="loading">
              <v-progress-circular
                indeterminate
                width="2"
                color="white"
                small
              ></v-progress-circular>
            </template>
            <template v-else>
              Cadastrar
            </template>
          </v-btn>
        </v-col>
      </v-row>
    </div>
    <barber-confirm-register-dialog ref="barberConfirmRegisterDialog" />
  </div>
</template>

<script>
import BarberConfirmRegisterDialog from './BarberConfirmRegisterDialog'
import InputField from '@/components/globais/form/input-field/InputField'
import FormMixin from '@/mixins/FormMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import ValidationMixin from '@/mixins/ValidationMixin'
export default {
  components: { BarberConfirmRegisterDialog, InputField },
  mixins: [ FormMixin, ValidationMixin, SnackbarMixin ],
  data: () => ({
    barbershopName: '',
    barberName: '',
    barberMail: '',
    barberPassword: '',
    loading: false,
  }),
  methods: {
    clearInputs () {
      this.barbershopName = null
      this.barberName = null
      this.barberMail = null
      this.barberPassword = null
    },
    register () {
      if (!this.isNameValid(this.barbershopName)) {
        this.showSnackbarErro('Por favor, preencha o nome do estabelecimento!')
      } else if (!this.isNameValid(this.barberName)) {
        this.showSnackbarErro('Por favor, informe o seu nome!')
      } else if (!this.isEmailValid(this.barberMail)) {
        this.showSnackbarErro('Por favor, informe um e-mail válido!')
      } else if (!this.isPasswordValid(this.barberPassword)) {
        this.showSnackbarErro('Sua senha deve ter no mínimo 6 caracteres!')
      } else {
        this.loading = true
        this.$axios.post(`${this.$url_api}/barbershop/register-barbershop-and-barber`, {
          barbershop_name: this.barbershopName,
          barber_name: this.barberName,
          barber_mail: this.barberMail,
          barber_password: this.barberPassword
        }).then(response => {
          if (Number(response.status) === 200) {
            if (Number(response.data.codigo) === 200) this.onRegisterSuccess()
            else this.onRegisterError(response.data.corpo) 
          } else this.onRegisterError()
        }).catch(() => this.onRegisterError())
        .finally (() => this.loading = false)
      }
    },
    onRegisterError (msg = 'Não foi possível realizar o cadastro!') {
      this.showSnackbarErro(msg)
    },
    onRegisterSuccess () {
      this.showSnackbar('Barbearia cadastrada com sucesso!')
      this.$refs.barberConfirmRegisterDialog.showDialog()
      this.clearInputs()
    }
  }
}
</script>

<style lang="scss" scoped>
.btn-register { height: 50px !important; width: 200px; border-radius: 0; }
.title { font-size: 1.5rem; font-weight: bold; }
.barber-register { padding: 0; }
.register-info {
  color: #fff;
  width: 500px;
  max-width: 100%;
  border-radius: 10px; 
  margin: auto;
}
@media all and (max-width: 959px) {
  .form-container {
    max-width: 100%;
    width: 600px;
  }
}
@media all and (max-width: 599px) {
  .title { font-size: 1.2rem; }
}
</style>