<template>
  <div class="autenticate">
    <landing-page-header
      btnIcon="login"
      btnText="Logar-se"
      image="fundo_autenticacao_barbeiro.png"
      topCardDescription="Agendamento 24h, Controle financeiro e divulgação do seu negócio!"
      @onClickTopCar="showLoginBox"
    />
    <barber-change-password-dialog ref="barberChangePasswordDialog" />
    <barber-login ref="barberLoginDialog" @recoverPassword="recoverPassword" />
    <barber-recover-password
      ref="barberRecoverPasswordDialog" 
      @onRecoverPassword="showChangePasswordDialog"
    />
    <barber-register />
    <app-footer />
  </div>
</template>

<script>
import AppFooter from '@/components/site/footer/AppFooter';
import BarberChangePasswordDialog from '@/components/auth/BarberChangePasswordDialog';
import BarberLogin from '@/components/auth/BarberLogin';
import BarberRecoverPassword from '@/components/auth/BarberRecoverPassword'
import BarberRegister from '@/components/auth/BarberRegister';
import LandingPageHeader from '@/components/site/header/LandingPageHeader';
export default {
  components: { AppFooter, BarberChangePasswordDialog, BarberLogin, BarberRecoverPassword, BarberRegister, LandingPageHeader },
  methods: {
    recoverPassword (email) {
      this.$refs.barberRecoverPasswordDialog.showDialog(email)
    },
    showLoginBox() {
      this.$refs.barberLoginDialog.showDialog()
    },
    showChangePasswordDialog () {
      this.$refs.barberChangePasswordDialog.showDialog()
    }
  }
}
</script>

<style scoped>
.autenticate {
  background: #000;
}
</style>
